button{
    height: 25px;
    border: none;
    border-radius: 10px;
    color: black;
    background-color: #ffffff;
}
#TopNav{
    background-color: #221e1e;
    width: 100%;
    height: 80px;
    box-shadow: 0 7px 7px rgba(54, 52, 52, 0.752);
}
#logo{
    display: inline-block;
    margin-top:10px;
    height: 60px;
    margin-left: 30px;
    margin-right: 50px;
}

body{
    background-image: url(../public/static/5127031.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: screen;
    overflow-y: auto;
}
.centerText{
    text-align: center;
}
.centerElem{
    margin: auto;
}
.RightAlign{
    float: right;
    margin-right: 20px;
}
.Button{
    width: auto;
    height: 30px;
    margin-top: 25px;
    font-size: 20px;
    border-radius: 5px;
    display: block;
}
#appName{
    cursor: pointer;
    display: block;
    margin-top: 20px;
    text-decoration: underline 1px;
    background-color: rgba(128, 170, 225, 0.85);
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 10px;
    max-width: 100%;
}
#title{
    text-align: center;
    margin: auto;
    margin-top: 30px;
    font-size: 18px;
    width: 60%;
    background-color: #f5f5f578;
    border-radius: 5px;
    border: 1px solid;
}
#shortUrl,#changePass{
    margin: auto;
    margin-top: 50px;
    width: max-content;
    background-color: #597e6574;
    padding: 30px;
    border-radius: 10px;
    max-width: 100%;
    box-shadow: 0 8px 25px 5px rgba(2, 2, 2, 0.765);
}
#shortBtn{
    font-size: 15px;
    margin: auto;
    margin-top: 40px;
    border: 1px solid;
    background-color: #c6d1e2d1;
}
#bottomDesc {
    margin: auto;
    margin-top: 50px;
    text-align: center;
    padding: 10px;
    width: 60%;
    border: 1px solid;
    border-radius: 10px;
    background-color: #f5f5f536;
    position: relative;
    bottom: 10px;
  }
#urlInput{
    outline: none;
    border: none;
    border-bottom: 1px solid;
    text-align: center;
    font-size: 18px;
    color:black;
    background-color: #597e6574;
    border-radius: 10px;
    margin-top: 20px;
}
::placeholder {
    text-align: center;
    font-size: 18px;
    letter-spacing: 1px;
    word-spacing: 2px;
    color: #faf6ed;
  }
.inputText{
    outline: none;
    border: none;
    border-bottom: 1px solid;
    font-size: 18px;
    color:black;
    background-color: #95ad9cba;
    border-top-right-radius: 3px;
    border-top-left-radius: 3px;
    margin-top: 20px;
}
.submitBtn{
    font-size: 15px;
    margin: auto;
    margin-top: 40px;
    border: 1px solid;
    background-color: rgb(154, 186, 241);
}
#savedLinks{
    margin: auto;
    padding: 10px;
    margin-top: 30px;
    background-color: #a1bd9e;
    text-align: center;
    width: max-content;
    border-radius: 10px;
}
#mobileTable{
    display: none;
}
body{
    margin: 0px;
}
textarea{
    background-color: #a1bd9e;
    outline: none;
    border: none;
    resize: vertical;
    color: black;
    height: auto;
}
@media only screen and (max-width: 600px) {
    #TopNav{
        background-color: #221e1e;
        width: 100%;
        height: 110px;
        box-shadow: 0 7px 7px rgba(54, 52, 52, 0.752);
    }
    #logo{
        height: 60px;
        padding-top: 10px;
        display: block;
        margin-left: 30px;
        margin: auto;
        border-bottom: 1px solid rgb(91, 87, 87);
    }
    #TopNav button{
        margin-top: 2px;
        margin-right: 10px;
    }
    #savedLinks{
        display: none;
    }
    #mobileTable{
        display: block;
        margin-top: 100px;
    }
    #appName{
        width: 100%;
    }
    #shortUrl{
        max-width: 80%;
    }
    #changePass{
        max-width: 80%;
    }
    .inputText{
        width: 100%;
    }
    textarea {
        box-sizing: border-box;
        height: auto !important;
        overflow: auto;
      }
      
    ::placeholder {
        color: #505768;
        font-size: 13px;
      }
    #title{
        width: 90%;
    }
    #bottomDesc{
        width: 90%;
    }
  }
#mobileTable table{
    border: 1px solid;
    margin-top: 20px;
    overflow: hidden;
    max-width: 100%;
    margin: auto;
}
#shortUrl table,#changePass table{
    text-align: right;
}
#shortUrl td,#changePass td{
    vertical-align: bottom;
}
.imgBtn{
    cursor: pointer;
}
